}


/* PDF Viewer stylings starts here */

.pdf-view {
    background-image: url(/portfolio/static/media/resume-background.f6f3aff1.jpg);
    background-size: cover;
    text-align: center;
    align-items: center;
    padding: 20px;
    display: flex;
    flex-direction: column;
    font-family: 'Acme', sans-serif;
    font-size: 15px;
}


.react-pdf__Page__textContent {
    display: none;
}

button.button-icon {
    margin: 5px;
}

.pdf-icon-view {
    display: flex;
}

.pdf-icon-item-icon {
    width: 80%;
}

.react-pdf__Page {
    max-height: 100%;
}


.react-pdf__Page__canvas {
    padding: 10px 0 0 0;
    margin: 0 auto;
    
}

  /* PDF Viewer stylings ends here */
  
@media only screen and (max-width: 1280px) {
