.banner-name {
    font-family: 'Acme', sans-serif;
    font-size: 90px;
    padding-top: 40px;
    text-align: center;
    color:#d5d0d0;
}

h1.intro-name {
  font-size: 60px;
}

h1.intro-title {
  font-size: 40px;
}

#js-container {
    width: 70vw;
    margin: 200px auto 70px auto;
}

.short-intro {
  font-size: 20px;
  color: #e0e2e4;
  width: 50%;
  margin: 150px auto 0 auto;
}

.contact-wrapper {
  display: flex;
  max-width: 50%;
  margin: 100px auto 0px auto;
}

.contact-icon-wrapper {
  display: flex;
  width: 50%;
}

.email-icon:hover, .phone-icon:hover {
  background-color: #dc0909;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
  letter-spacing: 0.8em;
}

.email-icon, .phone-icon  {
    font-size: 12px;
    color: white;
    padding: 14px 100px;
    margin: 1em auto;
    background-color: #da3737;
    border: none;
    border-radius: 19px;
    text-transform: uppercase;
    letter-spacing: 0.5em;
    -webkit-transition: all 0.2s cubic-bezier(.4,0,.2,1);
    transition: all 0.2s cubic-bezier(.4,0,.2,1);
 
}

@media (max-width: 768px) {
  h1.intro-name {
    font-size: 30px;
  }
  
  h1.intro-title {
    font-size: 15px;
  }
  
  #js-container {
      width: 70vw;
      margin: 100px auto 30px auto;
  }
}
  

@media only screen and (max-width: 2400px) and (min-width: 768px) and (orientation: portrait) {
