  h1 {
    text-align: center;
    color: #fff;
    font-size: 5em;
    letter-spacing: 8px;
    font-family: 'Press Start 2P';
    font-weight: 400;
    /*Create overlap*/
    
    margin: 0;
    line-height: 0;
    /*Animation*/
    
    -webkit-animation: glitch1 2.5s infinite;
    
            animation: glitch1 2.5s infinite;
  }
  
  h1:nth-child(2) {
    color: #67f3da;
    -webkit-animation: glitch2 2.5s infinite;
            animation: glitch2 2.5s infinite;
  }
  
  h1:nth-child(3) {
    color: #f16f6f;
    -webkit-animation: glitch3 2.5s infinite;
            animation: glitch3 2.5s infinite;
  }
  /*Keyframes*/
  
  @-webkit-keyframes glitch1 {
    0% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    7% {
      -webkit-transform: skew(-0.5deg, -0.9deg);
              transform: skew(-0.5deg, -0.9deg);
      opacity: 0.75;
    }
    10% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    27% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    30% {
      -webkit-transform: skew(0.8deg, -0.1deg);
              transform: skew(0.8deg, -0.1deg);
      opacity: 0.75;
    }
    35% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    52% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    55% {
      -webkit-transform: skew(-1deg, 0.2deg);
              transform: skew(-1deg, 0.2deg);
      opacity: 0.75;
    }
    50% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    72% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    75% {
      -webkit-transform: skew(0.4deg, 1deg);
              transform: skew(0.4deg, 1deg);
      opacity: 0.75;
    }
    80% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
    100% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
    }
  }
  
